<template>
  <el-container>
    <el-main v-loading="loading">
      <el-table :data="userFiledList">
        <el-table-column prop="id" label="人员ID" />
        <el-table-column prop="userName" label="姓名" />
        <el-table-column prop="nickName" label="微信昵称" />
        <el-table-column prop="address" label="地区" />
        <el-table-column prop="mobile" label="手机号码" />
        <el-table-column prop="remarks" label="备注" />
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="delMsg(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>确认删除该条信息？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
// import { userFiledInfo } from "../../../../data/userManage";
export default {
  name: "userfiledTable",
  props:['data', 'loading'],
  data() {
    return {
      // userFiledList: Array(8).fill(userFiledInfo),
      userFiledList: [],
      dialogVisible: false,
      id:''
    };
  },
  methods: {
    handleClick(params) {
      this.id = params.id;
      var datas = {
        type:'edit',
        id: this.id,
        state: true
      }
      this.$emit('dialogState', datas);
    },
    delMsg(params) {
      this.id = params.id;
      this.dialogVisible = true;
    },
    sure(){
      this.api.delete(`/groundPerson/${this.id}`).then(()=>{
        this.userFiledList = this.userFiledList.filter(item=>{
          return item.id !== this.id
        })
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
      this.dialogVisible = false
    } 
  },
  watch:{
    data(newValue){
      this.userFiledList = newValue;
    }
  }
};
</script>
<style lang="less" scoped>
</style>