<template>
  <el-dialog v-loading="loading" :title="activeheader" width="900px" :visible.sync="dialogVisible" :before-close="cancle">
    <div class="dialog-content">
      <div class="list">
        <span>驻场人员姓名：</span>
        <el-input class="com-input" placeholder="请输入驻场人员姓名" v-model="userName" />
      </div>
      <div class="list">
        <span>驻场人员手机号：</span>
        <el-input class="com-input" placeholder="请输入驻场人员姓名" v-model="mobile" />
      </div>
      <div class="list">
        <span>地区：</span>
        <user-address ref="addUserAddress" :defaultValue="defaultAddress" />
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancle">取 消</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </div>
  </el-dialog>
</template>
<script>
import UserAddress from "../../../../components/address";
import check from "utils/check";
let requireArray = ["userName", "mobile", "province", "city", "country"]; //必传字段
let headers = ['新增驻场人员','信息编辑'];

export default {
  name: "addUserfiled",
  props: ["type","id"],
  data() {
    return {
      dialogVisible: true,
      loading: false,
      userName:'',
      mobile:'',
      activeheader: headers[0],
      check: check.methods.check,
      checkTip: check.tips.checkTip,
      defaultAddress: null, //默认地址
    };
  },
  components: {
    UserAddress
  },
  mounted(){
    this.editInit();
  },
  methods: {
    editInit(){
      if( this.type === 'edit' ) this.activeheader = headers[1];
      if( !this.id ) return;
      this.loading = true;
      this.api.get(`/groundPerson/${this.id}`).then((res) => { //查询产品详情
        this.dataInit(res.data)
        setTimeout(() => {
          this.loading = false;
        }, 20);
      }).catch((err) => {
        console.log(err)
        this.loading = false;
        this.$message(err.message)
      })
    },
    dataInit(data){
      data = data || {};
      let { userName, mobile, id, province, city, country } = data;
      this.userName = userName;
      this.mobile = mobile;
      this.id = id;
      this.defaultAddress = {
        province,
        city,
        country
      }
    },
    submit() {
      let addressParams = this.$refs.addUserAddress.getData();
      let params = {
        userName: this.userName,
        mobile: this.mobile
      };
      let param = this.lodash.assign({}, params, addressParams);
      this.check(param, requireArray).then(()=>{
        this.addUser(param) 
      },()=>{ 
        this.$message(this.checkTip) 
      })
      .catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    editUser(param){ //修改用户
      param.id = this.id;
      this.api.put('/groundPerson', param, 'form').then((res)=>{
        if(res.data.code === this.ok){
          this.$emit('search');  
          this.$emit('dialogState', {state: false});
        }
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    addUser(param) { //添加用户
      console.log(this.header,'oooooooo')
      if(this.header === '信息编辑') return this.editUser(param);
      this.api.post("/groundPerson", param, 'form').then(res => {
        this.$emit('search');  
        this.$emit('dialogState', {state: false});
      })
      .catch(err => {
        console.log(err);
        this.$message(err.data.message);
      });
    },
    cancle() {
      this.$emit('dialogState', {state: false});
    }
  }
};
</script>
<style lang="less" scoped>
.dialog-content{
  .el-input{
    width: 300px;
  }
  .list{
    display: flex;
    height: 40px;
    line-height: 40px;
    margin-bottom: 20px;
    span{
      display: inline-block;
      min-width: 120px;
      text-align: right;
    }
  }
}
</style>